/* Smartphones (portrait) ----------- */
@media only screen and (max-width : 100px) {
.grid-container {
	display: grid;
        /* grid-gap: 20px; */
        grid-gap: 1vh;
        /* This puts 4 columns in a row */
        grid-template-columns: auto auto;
        font: "Century Gothic", Futura, sans-serif;
        font-size: min(1vw, 1vh);
	text-align: center;
        font-weight: 600;

        margin-left: auto;
        margin-right: auto;
        /*width: 80%;
        height: 80%;*/
        justify-items: center;

        /* This forces the grid elements to be square */
        grid-template-columns: 1fr 1fr;
}
}

/* Smartphones (landscape) ----------- */
@media only screen and (min-width : 321px) {
/* Styles */
.grid-container {
	display: grid;
	/* grid-gap: 20px; */
        grid-gap: 1vw;	
	/* This puts 4 columns in a row */
	grid-template-columns: auto auto auto auto;
	font: 1.5vw "Century Gothic", Futura, sans-serif;
	/* font-size: min(3vw, 3vh); */
	text-align: center; 
	font-weight: 600;

	margin-left: auto;
	margin-right: auto;
	/*width: 80%;
	height: 80%;*/
	justify-items: center;
	
	/* This forces the grid elements to be square */
	grid-template-columns: 1fr 1fr 1fr 1fr;
}
}  


